      url('../../../shared/assets/fonts/Inter-Regular.woff2') format('woff2'),
      url('../../../shared/assets/fonts/Inter-Regular.woff') format('woff'),
      url('../../../shared/assets/fonts/Inter-Regular.ttf') format('truetype');
